@import "../../variables";

.wordContainerMobile {
  width: 60vw;
  height: 20vh;
  border-bottom: 1px solid white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin-top: 2vh;

  h1 {
    font-family: "Montserrat", sans-serif;
    font-size: 4vh;
    color: white;
    letter-spacing: 5px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 15vh;
    width: 100vw;
    text-align: center;

    P {
      margin-top: 1vh;
      font-style: italic;
      font-weight: 300;
      font-size: 20px;
      margin-right: 10px;
    }
  }

  img {
    max-width: 60px;
    max-height: 100px;
    margin-left: 10px;
  }
}
