@import "../../variables";

.keyboardContainer {
  width: 95vw;
  padding: 5px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1vh;
}

.keysContainer {
  position: relative;
  width: 95vw;
  border-radius: 20px;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  overflow-y: scroll;
}

.optionsKeys {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 5px;
  width: 90vw;
  margin-top: 1vh;
}
